.product{
    background-color: white;
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding:20px;   /*padding modified from 20px to 30x*/
    width:100%;
    max-height:400px;
    min-width: 100px;;
    z-index:1;
}
.productRating{
    display:flex;
    
}
.productImage{
    max-height:200px ;
    width:100%;
    object-fit:contain;
    margin-bottom:15px;
}
.productInfo{
    height:100px;
    margin-bottom:15px;
}
.productPrice{
    margin-top:5px;
}
.productButton{
     background:#f0c14b;
    border:1px solid;
    border-radius:2px;
    height:25px;
    width:100px;
    margin-top:20px;
    margin-right: 20px;
    border-color: #a88734 #9c7e31 #846a29;
    color:#111;
}